import { Component, ElementRef, HostListener, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CartService } from 'src/app/services/cart.service';
import { ModalService } from 'src/app/services/modal.service';
import { BancobrasilService } from 'src/app/services/payments/bancobrasil.service';
import { MundipaggService } from 'src/app/services/payments/mundipagg.service';
import { PaypalService } from 'src/app/services/payments/paypal.service';
import { PicpayService } from 'src/app/services/payments/picpay.service';
import { PedidoService } from 'src/app/services/pedido.service';
import { UserService } from 'src/app/services/user.service';
import { MdAlertComponent } from '../../modais/md-alert/md-alert.component';
import { MdConfirmComponent } from '../../modais/md-confirm/md-confirm.component';
import { copyMessage, formatDateToISO, validateCC, validateCpfCnpj, validateDate } from '../../../core/Functions';
import { GoogleAnalyticsService } from 'src/app/services/googletag.service';
import { environment } from 'src/environments/environment';

declare var $: any;
declare var paypal: any;
declare var grecaptcha: any;

@Component({
  selector: 'app-pagamento',
  templateUrl: './pagamento.component.html',
  styleUrls: ['./pagamento.component.scss']
})
export class PagamentoComponent implements OnInit, OnDestroy {
  public valorMinimo = 1;
  formGeral: FormGroup;
  dadosCC: FormGroup;
  dataEntrega: string;
  timerPicPay: any;
  public codCupom;
  public qrCodePicPay: string;
  public qrCodePix: string;
  public linkPix: string;
  private timerPix: any;
  public msgSucessoCopy = false;
  public picpayTabActive = null;
  public loadingCupom = false;
  nsu = null;
  public disableFinalizarBtn = false;
  public errors = [];
  private bGetStatusPix = true;
  private bGetStatusPicPay = true;
  public bPayPalBtn = true;
  public layout = environment.layout;
  public cupomAplicado = false;

  constructor(private fb: FormBuilder,
              public userS: UserService,
              public cartS: CartService,
              private router: Router,
              private picPayS: PicpayService,
              private pedidoS: PedidoService,
              private mundipaggS: MundipaggService,
              private paypalS: PaypalService,
              private modalS: ModalService,
              private bancoBrasilS: BancobrasilService,
              private gTagS: GoogleAnalyticsService
              ) { }

  @ViewChild('paypalRef', { static: true }) private paypalRef: ElementRef;
  @HostListener('window:beforeunload', ['$event'])
  beforeUnloadHander(event) {
    return false;
  }
  ngOnInit(): void {
    this.renderBtnPayPal();

    if (this.cartS.cupom) {
      this.codCupom = this.cartS.cart.cupom.cupom;
      this.cupomAplicado = true;
    } else {
      this.cartS.desconto = 0;
      this.cartS.cupom = null;
      this.cupomAplicado = false;
    }
    $(() => {
      $('[data-toggle="popover"]').popover();
    });

    if (innerHeight < 850) {
      scrollTo(0, 300);
    }
    this.setupForm();
    this.setValorTotal();

    this.formGeral.patchValue({entregaForm: this.cartS.cart.entrega});
    this.dataEntrega = new Date(this.formGeral.value.entregaForm.data + 'T23:59').toLocaleDateString();
    // this.gTagS.beginCheckout(this.cartS.cart.item);
    this.pedidoS.postPedido(this.cartS.cart, 'Abandonado').subscribe((res: any) => {});
  }

  get form() {
    return this.formGeral.value;
  }

  get formEntr() {
    return this.formGeral.value.entregaForm;
  }

  setupForm() {
    this.formGeral = this.fb.group({
      entregaForm: [null],
    });

    this.dadosCC = this.fb.group({
      parcelas: [1, Validators.required],
      digiNumero: [null, Validators.required],
      digiNome: [null, Validators.required],
      digiMes: [null, Validators.required],
      digiAno: [null, Validators.required],
      digiCVV: [null, Validators.required],
      valor: [this.cartS.cart.valorTotal || null, Validators.required]
    });
  }

  resetarCarrinho() {
    this.modalS.createModal(MdConfirmComponent, { title: 'Confirmar', message: 'Deseja limpar o carrinho e reiniciar o processo do zero?' }).then(res => {
      if (res) {
        this.cartS.resetCart();
        this.router.navigate(['/']);
      }
    });
  }

  editDadosEntrega() {
    this.modalS.createModal(MdConfirmComponent, { title: 'Confirmar', message: 'Deseja editar os dados da entrega?' }).then(res => {
      if (res) {
        this.router.navigate(['/entrega']);
      }
    });
  }

  clickTabs($event) {
    if ($event.target.id === 'nav-paypal-tab') {
      this.renderBtnPayPal();
    }
  }

  renderBtnPayPal() {
    this.paypalS.checkDadosCart(this.cartS.cart).subscribe((resCheck: any) => {
      if (resCheck.error) {
        this.modalS.createModal(MdAlertComponent, { title: 'Erro ao finalizar pagamento.', message: resCheck.error });
        this.bPayPalBtn = false;
        return;
      }
      if ($('.paypal-buttons').length === 0) {
        paypal.Buttons({
          style: {
            layout: 'horizontal'
          },
          createOrder: (data, actions) => {
            return actions.order.create({
              purchase_units: [{
                amount: {
                  value: this.cartS.cart.valorTotal,
                  currency_code: 'BRL'
                }
              }]
            });
          },
          onApprove: (data, actions) => {
            actions.order.capture().then(resCapture => {
              this.configCart('paypal', resCapture);
              this.pedidoS.postPedido(this.cartS.cart).subscribe((resPostPedido: any) => {
                if (resPostPedido.error) {
                  console.error(resPostPedido.error);
                  return;
                }
                this.gTagS.purchase(this.cartS.cart, resPostPedido.id);
                this.resetDadosCart();
                this.router.navigate(['/order-success']);
              });
            });
          },
          onError: err => {
            console.error(err);
          }
        }).render(this.paypalRef.nativeElement);
      }
    });
  }

  copyToClipboard() {
    copyMessage(this.linkPix);
    this.msgSucessoCopy = true;
  }

  async gerarPix($event) {
    if (!this.validarCart()) {
      return;
    }
    this.setValorTotal();
    const pixGerado = await this.bancoBrasilS.gerarPix(this.cartS.cart.valorTotal, this.userS.user, this.cartS.cart, $event).toPromise<any>();
    if (pixGerado.error) {
      this.modalS.createModal(MdAlertComponent, {title: 'Erro ao gerar pagamento.', message: pixGerado.error});
      return;
    }
    this.qrCodePix = pixGerado.base64QRcode;
    this.linkPix = pixGerado.textoImagemQRcode;
    this.timerPix = setInterval(async () => {
      const resGet = await this.bancoBrasilS.getPix(pixGerado.txid).toPromise<any>();
      switch (resGet.status) {
        case 'ATIVA':
          break;
        case 'CONCLUIDA':
          if (this.bGetStatusPix) {
            this.bGetStatusPix = false;
            this.configCart('pix', resGet);
            clearInterval(this.timerPix);
            const resPostPedido = await this.pedidoS.postPedido(this.cartS.cart).toPromise<any>();
            if (resPostPedido.error) {
              console.error(resPostPedido.error);
              return;
            }
            this.gTagS.purchase(this.cartS.cart, resPostPedido.id);
            this.resetDadosCart();
            this.router.navigate(['/order-success']);
          }
          clearInterval(this.timerPix);
          break;
        default:
          this.modalS.createModal(MdAlertComponent, {title: 'QrCode expirado ou invÃ¡lido.', message: 'QrCode expirado ou invÃ¡lido. Recarregue a pÃ¡gina e tente novamente. Obs: Se o pagamento jÃ¡ foi concluÃ­do e vocÃª estÃ¡ vendo esta mensagem, entre em contato com a nossa equipe.'});
          clearInterval(this.timerPix);
          break;
      }
    }, 6000);
  }

  async gerarQrCodePicPay($event) {
    if (!this.validarCart()) {
      return;
    }
    this.setValorTotal();
    const payload = {
      cart: this.cartS.cart,
      value: this.cartS.cart.valorTotal,
      referenceId: Date.now()
    };

    const qrCodeGerado = await this.picPayS.gerarQrCode(payload, $event).toPromise<any>();
    if (qrCodeGerado.error) {
      this.modalS.createModal(MdAlertComponent, {title: 'Erro ao gerar pagamento.', message: qrCodeGerado.error});
      return;
    }
    this.qrCodePicPay = qrCodeGerado.qrcode.base64;
    this.timerPicPay = setInterval(async () => {
      const resStatus = await this.picPayS.status(qrCodeGerado.referenceId).toPromise<any>();
      switch (resStatus.status) {
        case 'created':
          break;
        case 'paid':
          if (this.bGetStatusPicPay) {
            this.bGetStatusPicPay = false;
            this.configCart('picpay', resStatus);
            clearInterval(this.timerPicPay);
            const resPostPedido = await this.pedidoS.postPedido(this.cartS.cart).toPromise<any>();
            if (resPostPedido.error) {
              console.error(resPostPedido.error);
              return;
            }
            this.gTagS.purchase(this.cartS.cart, resPostPedido.id);
            this.resetDadosCart();
            this.router.navigate(['/order-success']);
          }
          clearInterval(this.timerPicPay);
          break;
        case 'expired':
          clearInterval(this.timerPicPay);
          this.modalS.createModal(MdAlertComponent, {title: 'QrCode expirado', message: 'Gere um QrCode e tente novamente'});
          break;
        default:
          this.modalS.createModal(MdAlertComponent, {title: 'QrCode expirado ou invÃ¡lido.', message: 'QrCode expirado ou invÃ¡lido. Recarregue a pÃ¡gina e tente novamente. Obs: Se o pagamento jÃ¡ foi concluÃ­do e vocÃª estÃ¡ vendo esta mensagem, entre em contato com a nossa equipe.'});
          clearInterval(this.timerPicPay);
          break;
      }
    }, 6000);
  }

  finalizarCompra($event) {
    if (!this.validarCart()) {
      return;
    }
    this.setValorTotal();
    const user = this.userS.user;
    if (user.pais === 'Brasil') {
      if (!user.cpf && !user.cnpj) {
        this.modalS.createModal(MdAlertComponent, { title: 'Cadastro incompleto.', message: 'Cadastro incompleto, favor entrar na pÃ¡gina de cadastro no menu Minha Conta e preencher os dados necessÃ¡rios.'});
        return;
      }
    }

    if ((!user.cep && !user.cobrCep) || (!user.fone && !user.fone2 && !user.fonecelular) || (!user.cobrNumero && !user.numero) || !user.email) {
      this.modalS.createModal(MdAlertComponent, { title: 'Cadastro incompleto.', message: 'Cadastro incompleto, favor entrar na pÃ¡gina de cadastro no menu Minha Conta e preencher os dados necessÃ¡rios.'});
      return;
    }
    this.errors = [];
    if (document.getElementById('nav-picpay').classList.contains('active')) {
      this.modalS.createModal(MdAlertComponent, {title: 'PicPay', message: `Use o botÃ£o 'Gerar QR Code' acima para pagar com o PicPay.`});
      return;
    }
    if (document.getElementById('nav-paypal').classList.contains('active')) {
      this.modalS.createModal(MdAlertComponent, {title: 'PayPal', message: `Use o botÃ£o 'PayPal' acima para pagar com o PayPal.`});
      return;
    }
    if (document.getElementById('nav-pix').classList.contains('active')) {
      this.modalS.createModal(MdAlertComponent, {title: 'Pix', message: `Use o botÃ£o 'Gerar QR Code' acima para pagar com o Pix.`});
      return;
    }
    if (this.dadosCC.invalid) {
      this.modalS.createModal(MdAlertComponent, {title: 'Dados do cartÃ£o invÃ¡lidos.', message: 'Verifique os dados do cartÃ£o e tente novamente.'});
      this.errors.push('Verifique os dados do cartÃ£o e tente novamente.');
      return;
    }
    if (!validateCC(this.dadosCC.value.digiNumero)) {
      this.modalS.createModal(MdAlertComponent, {title: 'NÃºmero do cartÃ£o invÃ¡lido.', message: 'Verifique o nÃºmero do cartÃ£o e tente novamente. Caso o erro persista, entre em contato com a nossa equipe.'});
      this.errors.push('Verifique o nÃºmero do cartÃ£o e tente novamente. Caso o erro persista, entre em contato com a nossa equipe.');
      return;
    }
    if (!validateCpfCnpj(this.userS.user.cpf) && !validateCpfCnpj(this.userS.user.cnpj)) {
      this.modalS.createModal(MdAlertComponent, {title: 'CPF/CNPJ invÃ¡lido.', message: 'Verique seu CPF/CNPJ na pÃ¡gina de cadastro e tente novamente. Caso o erro persista, entre em contato com a nossa equipe.'});
      this.errors.push('Verique seu CPF/CNPJ na pÃ¡gina de cadastro e tente novamente. Caso o erro persista, entre em contato com a nossa equipe.');
      return;
    }
    this.modalS.createModal(MdConfirmComponent, { title: 'Confirmar Pagamento', message: 'Deseja confirmar o pagamento?' }).then(confirm => {
      if (confirm) {
        try {
          this.disableFinalizarBtn = true;
          this.configCart('credit_card');
          grecaptcha.ready(() => {
            grecaptcha.execute(environment.recaptchaToken, {action: 'CHECKOUT'}).then((token) => {
              this.mundipaggS.gerarPagamentoCC(this.dadosCC.value, token).subscribe((res: any) => {
                if (res.error) {
                  this.modalS.createModal(MdAlertComponent, {title: 'Erro ao gerar pagamento.', message: res.error});
                  this.errors.push(res.error);
                  this.disableFinalizarBtn = false;
                  if (res.error.startsWith('NÃºmero de tentativas excedido')) {
                    this.userS.excluirConta(this.userS.user.id).subscribe(() => {
                      this.userS.logoff();
                    });
                  }
                  return;
                }
                this.router.navigate(['/order-success']);
                this.gTagS.purchase(this.cartS.cart, res.id);
                this.resetDadosCart();
              });
            });
          });
        } catch (error) {
          this.modalS.createModal(MdAlertComponent, {title: 'Erro ao gerar pagamento.', message: error.message });
          this.errors.push(error.message);
          this.disableFinalizarBtn = false;
        }
      }
    });
  }

  validarCart(): boolean {
    if (this.cartS.cart.valorTotalItems < this.valorMinimo) {
      this.modalS.createModal(MdAlertComponent, { title: 'Valor mÃ­nimo nÃ£o atingido.', message: `Valor mÃ­nimo nÃ£o atingido. Faltam R$ ${(this.valorMinimo - this.cartS.cart.valorTotalItems).toFixed(2)} em produtos para fechar sua compra.` });
      return false;
    }
    if (this.cartS.cart.entrega.entregaBool) {
      const calendarDate = new Date(this.cartS.cart.entrega.data + 'T23:59');
      const today = new Date();
      if (calendarDate.getDate() + calendarDate.getMonth() === today.getDate() + today.getMonth()) {
        // if (this.formGeral.value.periodo === '13') {
        //   this.errors.push('HorÃ¡rio corte de domingos e feriados (11h) atingido. Favor escolher uma data posterior a atual.');
        // }
        if (today.getDay() === 0) {
          if (today.getHours() >= 12) {
            this.modalS.createModal(MdAlertComponent, { title: 'Horario de corte atingido.', message: 'HorÃ¡rio de corte dos domingos (12h) atingido, favor escolher uma data posterior a atual.' });
            return false;
          }
        } else {
          // tslint:disable-next-line:triple-equals
          if ((today.getHours() >= 10 || (today.getHours() === 9 && today.getMinutes() >= 30)) && (this.cartS.cart.entrega.periodo == 12 || this.cartS.cart.entrega.periodo == 1)) {
            this.modalS.createModal(MdAlertComponent, { title: 'Horario de corte atingido.', message: 'HorÃ¡rio de corte (09h30min) atingido para o perÃ­odo da manhÃ£, favor escolher o perÃ­odo da tarde ou uma data posterior a atual.' });
            return false;
          }
          else if (today.getHours() >= 16) {
            this.modalS.createModal(MdAlertComponent, { title: 'Horario de corte atingido.', message: 'HorÃ¡rio de corte (16h) atingido, favor escolher uma data posterior a atual.' });
            return false;
          }
        }
      }
    }
    return true;
  }

  configCart(paymentMethod: string, retornoAdquirente?: any) {
    if (!validateDate(this.formGeral.value.entregaForm.data)) {
      this.formGeral.patchValue({ entregaForm: { data: formatDateToISO(this.formGeral.value.entregaForm.data) }});
    }
    const pagamento = {
      nsu: this.nsu,
      parcelas: null,
      method: paymentMethod,
      retornoAdquirente: retornoAdquirente || null
    };
    if (paymentMethod === 'credit_card') {
      pagamento.nsu = this.nsu;
      pagamento.parcelas = this.dadosCC.value.parcelas;
    }
    this.cartS.cliente = this.userS.user;
    this.setValorTotal();
    this.cartS.pagamento = pagamento;
    if (this.cartS.cart.cupom) {
      this.cartS.desconto = this.cartS.cart.valorTotalItems - this.cartS.cart.valorTotal + this.cartS.cart.entrega.vFrete;
    }
  }

  aplicarCupom() {
    if (!this.codCupom) {
      return;
    }
    this.loadingCupom = true;
    try {
      this.pedidoS.cupomDesconto(this.codCupom).subscribe((res: any) => {
        this.loadingCupom = false;
        if (res.error) {
          this.modalS.createModal(MdAlertComponent, {title: 'Cupom de Desconto', message: res.error});
          return;
        }
        this.cartS.cupom = res[0];

        if (res[0].tipo === 'VALOR') {
          this.cartS.desconto = res[0].valor;
        }
        if (res[0].tipo === 'INDICE') {
          this.cartS.desconto = this.cartS.cart.valorTotalItems * res[0].valor / 100;
        }
        this.setValorTotal();
        this.renderBtnPayPal();
        this.cupomAplicado = true;
      });
    } catch (error) {
      this.loadingCupom = false;
      this.cupomAplicado = false;
    }
  }

  retirarCupom() {
    this.modalS.createModal(MdConfirmComponent, {title: 'Cupom de Desconto', message: 'Deseja retirar o cupom de desconto?'}).then(confirm => {
      if (confirm) {
        this.codCupom = null;
        this.cartS.cupom = null;
        this.cartS.desconto = 0;
        this.setValorTotal();
        this.renderBtnPayPal();
        this.cupomAplicado = false;
      }
    });
  }

  setValorTotal() {
    this.cartS.valorTotal = Math.round(((this.cartS.cart.valorTotalItems + this.cartS.cart.entrega.vFrete - this.cartS.cart.desconto) + Number.EPSILON) * 100) / 100;
    this.dadosCC.patchValue({valor: this.cartS.cart.valorTotal});
  }

  resetDadosCart() {
    this.cartS.resetCart();
  }

  ngOnDestroy() {
    clearInterval(this.timerPicPay);
    clearInterval(this.timerPix);
    this.cartS.desconto = 0;
    this.cartS.cupom = null;
  }
}
