import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BasicLayoutComponent } from './layouts/basic-layout/basic-layout.component';
import { CadastroComponent } from './pages/cliente/cadastro/cadastro.component';
import { HomeComponent } from './pages/home/home.component';
import { LoginComponent } from './pages/login/login.component';
import { ProdutoComponent } from './pages/produto/produto.component';
import { AuthGuard } from './services/auth.guard';
import { MinhaContaComponent } from './pages/cliente/minha-conta/minha-conta.component';
import { EnderecosComponent } from './pages/cliente/enderecos/enderecos.component';
import { PedidosComponent } from './pages/cliente/pedidos/pedidos.component';
import { CursosComponent } from './pages/cliente/cursos/cursos.component';
import { ListasDesejosComponent } from './pages/cliente/listas-desejos/listas-desejos.component';
import { CarrinhoComponent } from './pages/checkout/carrinho/carrinho.component';
import { StatusPedidoComponent } from './pages/cliente/status-pedido/status-pedido.component';
import { ContatoComponent } from './pages/contato/contato.component';
import { ImprensaComponent } from './pages/imprensa/imprensa.component';
import { QuemSomosComponent } from './pages/quem-somos/quem-somos.component';
import { NossasLojasComponent } from './pages/nossas-lojas/nossas-lojas.component';
import { DuvidasFrequentesComponent } from './pages/duvidas-frequentes/duvidas-frequentes.component';
import { TrabalheConoscoComponent } from './pages/trabalhe-conosco/trabalhe-conosco.component';
import { NewsletterComponent } from './pages/newsletter/newsletter.component';
import { ListaComponent } from './pages/lista/lista.component';
import { PagamentoComponent } from './pages/checkout/pagamento/pagamento.component';
import { DepoimentosComponent } from './pages/depoimentos/depoimentos.component';
import { EntregaComponent } from './pages/checkout/entrega/entrega.component';
import { RedefinirSenhaComponent } from './pages/redefinir-senha/redefinir-senha.component';
import { SuccessComponent } from './pages/checkout/success/success.component';
import { FloresGuard } from './services/flores.guard';
import { RedirectNewsletterComponent } from './pages/newsletter/redirect-newsletter/redirect-newsletter.component';

const routes: Routes = [
  {path: '', component: BasicLayoutComponent, children: [
    {path: 'sao-paulo', component: HomeComponent, canActivate: [FloresGuard]},
    {path: 'berrini', component: HomeComponent, canActivate: [FloresGuard]},
    {path: 'gardencenter', component: HomeComponent, canActivate: [FloresGuard]},
    {path: '', component: HomeComponent},
    {path: 'produto/:slug', component: ProdutoComponent},
    {path: 'cliente', canActivate: [AuthGuard],
    children: [
      {path: 'cadastro', component: CadastroComponent},
      {path: 'minha-conta', component: MinhaContaComponent},
      {path: 'enderecos', component: EnderecosComponent},
      {path: 'pedidos', component: PedidosComponent},
      {path: 'cursos', component: CursosComponent},
      {path: 'listas-desejos', component: ListasDesejosComponent},
      {path: 'status-pedido/:id', component: StatusPedidoComponent}
    ]},
    {path: 'carrinho', component: CarrinhoComponent},
    {path: 'carrinho/:id', component: CarrinhoComponent},
    {path: 'login', component: LoginComponent},
    {path: 'contato', component: ContatoComponent},
    {path: 'imprensa', component: ImprensaComponent},
    {path: 'quem-somos', component: QuemSomosComponent},
    {path: 'nossas-lojas', component: NossasLojasComponent},
    {path: 'duvidas-frequentes', component: DuvidasFrequentesComponent},
    {path: 'trabalhe-conosco', component: TrabalheConoscoComponent},
    {path: 'newsletter', component: NewsletterComponent},
    {path: 'newsletter-redirect', component: RedirectNewsletterComponent},
    {path: 'lista/:tipo/:termo', component: ListaComponent},
    {path: 'depoimentos', component: DepoimentosComponent},
    {path: 'redefinir-senha/:code', component: RedefinirSenhaComponent},
    {path: 'order-success', component: SuccessComponent}
  ]},
  {path: 'pagamento', component: PagamentoComponent},
  {path: 'entrega', component: EntregaComponent},
  {path: '**', redirectTo: ''}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {scrollPositionRestoration: 'enabled', anchorScrolling: 'enabled'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
